.actions {
  text-align: end;
  background: #f6f7f9;
}
.actions button {
  cursor: pointer;
  outline: none;
  border: none;
  font-size: 12px;
  text-transform: uppercase;
  transition: color 0.3s;
  padding: 3px 10px;
  border-left: 1px solid #ced4de;
}

.codeBox {
  border: 1px solid #e9e9e9;
  border-radius: 6px;
  display: inline-block;
  width: 100%;
  position: relative;
  margin: 0 0 16px 0;
  transition: all 0.5s ease;
  overflow: hidden;
}
.codeBox:hover {
  border-color: #ccc;
}
.codeBox:target {
  border: 1px solid rgba(45, 183, 245, 0.7);
  box-shadow: 0 0 4px rgba(45, 183, 245, 0.5);
}
.codeBox:hover .meta, .codeBox:target .meta {
  background: #fbfbfb;
}
.codeBox:hover .title, .codeBox:target .title {
  background: #fbfbfb;
  box-shadow: 0 -1.2px 0 #e9e9e9;
}
.codeBox .title a,
.codeBox .title a:hover {
  color: #666;
}
.codeBox .demo {
  padding: 42px 20px 50px;
  border-bottom: 1px solid #CED4DE;
}
.codeBox .highlight {
  overflow: hidden;
  padding: 5px;
  border-top: 1px dashed #e9e9e9;
}
.codeBox pre {
  margin: 0;
  width: auto;
}