.nav {
  padding-left: 20px;
  width: 100%;
  height: 60px;
  line-height: 60px;
  background-color: #f5f6f7;
}

.container {
  display: flex;
}

.main {
  width: 100%;
  padding: 32px;
}
.main :global .preview {
  margin-top: -1rem;
}

.layout {
  box-sizing: border-box;
  margin: 16px 170px 20px 60px;
}

.fixNav {
  position: fixed;
  top: 50px;
  right: 2px;
  width: 150px;
  padding: 0;
  border-radius: 5px;
  list-style: none;
  background-color: #fff;
  border-left: 1px solid #e9e9e9;
}
.fixNav li {
  position: relative;
  border-left: 3px solid #fff;
  padding: 0 10px;
  line-height: 26px;
  font-size: 12px;
}
.fixNav li:last-child {
  border-bottom: 0;
}
.fixNav a {
  color: #2D3747;
  text-decoration: none;
  display: block;
}
.fixNav a:link, .fixNav a:visited {
  color: #2D3747;
}

.selected:before {
  content: "";
  position: absolute;
  display: block;
  top: 5px;
  left: -4px;
  height: 1rem;
  background-color: #0B5FFF;
  -webkit-transition: width 200ms ease 0s;
  transition: width 200ms ease 0s;
  width: 2px;
  border-radius: 1px;
}